body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
h1,h2 {
  margin: 0px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

hr {
  border-color: #b3b3b3;
  border-width: 0.5px;
  opacity: 0.5; }

.page-enter.in-transition {
  opacity: 0;
  -webkit-transform: translateX(1000px);
          transform: translateX(1000px); }

.page-enter-active.in-transition {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms; }

.page-exit.in-transition {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0); }

.page-exit-active.in-transition {
  opacity: 0;
  -webkit-transform: translateX(1000px);
          transform: translateX(1000px);
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms; }

.page-enter.out-transition {
  opacity: 0;
  -webkit-transform: translateX(-1000px);
          transform: translateX(-1000px); }

.page-enter-active.out-transition {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms; }

.page-exit.out-transition {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0); }

.page-exit-active.out-transition {
  opacity: 0;
  -webkit-transform: translateX(-1000px);
          transform: translateX(-1000px);
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms; }

.card {
  background-color: white;
  border-radius: 3px;
  overflow: hidden; }
  .card.padding {
    padding: 40px; }
  .card.shadow {
    box-shadow: 0px 1px 3px #e0e0e0; }
  .card > .header {
    padding: 10px 20px;
    border-bottom: 1px solid #f1f1f1; }
    .card > .header h1 {
      text-transform: uppercase;
      font-size: 12px; }
  .card > .content {
    padding: 20px; }

.dashboard-page .avatar {
  margin-right: 10px;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms; }
  .dashboard-page .avatar img {
    width: 50px;
    height: 50px;
    object-fit: cover;
    border-radius: 25px; }

.dashboard-page .name {
  font-weight: normal;
  transition: font-weight 300ms, -webkit-transform 300ms;
  transition: transform 300ms, font-weight 300ms;
  transition: transform 300ms, font-weight 300ms, -webkit-transform 300ms; }

.dashboard-page .card {
  transition: background-color 300ms;
  cursor: pointer; }
  .dashboard-page .card:hover {
    background-color: #2162b8;
    color: white; }
    .dashboard-page .card:hover .avatar {
      -webkit-transform: scale(1.2);
              transform: scale(1.2); }
    .dashboard-page .card:hover .name {
      font-weight: bold;
      -webkit-transform: translateX(15px);
              transform: translateX(15px); }

div.flexbox {
  display: -webkit-flex;
  display: flex; }



.header-container {
  height: 40px;
  padding: 15px;
  box-shadow: 0px 0px 3px 0px #a5a5a5;
  background-color: #2162b8;
  color: white; }
  .header-container h2 {
    text-transform: uppercase;
    font-size: 32px; }

.btn {
  border-radius: 4px;
  color: white;
  text-transform: capitalize;
  background-color: #2162b8;
  padding: 10px 20px;
  font-weight: bold;
  font-size: 14px; }

.drawer-container {
  height: calc(100% - 40px);
  padding: 10px; }
  .drawer-container ul {
    padding: 0px;
    list-style: none; }
  .drawer-container .main-menu li {
    padding: 5px 0px; }
  .drawer-container li {
    text-transform: uppercase;
    font-weight: bolder;
    padding: 2px 0px; }
    .drawer-container li a {
      text-decoration: none;
      color: black;
      font-size: 22px;
      font-style: italic; }
      .drawer-container li a.active {
        color: #2162b8; }
    .drawer-container li:hover a {
      color: #2162b8; }
  .drawer-container h3 {
    font-size: 15px;
    color: #7e8692;
    margin-bottom: 5px;
    margin-top: 5px; }
  .drawer-container ul {
    margin-top: 0px;
    margin-bottom: 20px; }


.label {
  padding: 5px;
  border-radius: 16px;
  height: 12px; }
  .label .text {
    line-height: 12px;
    font-size: 12px;
    font-weight: bold; }

.detail-page .back-btn .flexbox {
  cursor: pointer; }
  .detail-page .back-btn .flexbox:hover {
    background-color: #f1f1f1; }

.about-page h3 {
  font-size: 16px; }

.about-page p {
  font-size: 12px;
  color: #979797; }

.input-container label {
  font-size: 14px;
  font-weight: bold;
  color: #7e8692;
  margin-bottom: 10px;
  margin-top: 10px; }

.input-container {
  -webkit-flex-direction: column;
          flex-direction: column;
  display: -webkit-flex;
  display: flex; }
  .input-container input {
    border: #8ebdfa 1px solid;
    border-radius: 3px;
    transition: all ease-in-out 0.5s;
    font-size: 14px;
    padding: 10px;
    outline: none;
    box-shadow: 0px 1px 3px #e0e0e0; }
  .input-container input:active, .input-container input:hover {
    border: #2162b8 1px solid; }
  .input-container label .require {
    color: #bb0000;
    margin-left: 4px; }

